	module.exports = {"data":{"markdownRemark":{"html":"<p><a href=\"https://www.useloom.com/\">https://www.useloom.com/</a></p>\n<p>Loom is a chrome extension for creating quick videos. It’s incredibly handy for customer service, sales, bug reports and a dozen other use cases. The company is backed by Slack and General Catalyst.</p>\n<p>While at Loom I rebuilt their on boarding process and their settings panel in React. Both pages required extensive rewrites which touched every part of the technology stack.</p>\n<p><img src=\"/images/loom/loom.png\" alt=\"alt text\" title=\"loom logo\">\n<img src=\"/images/loom/get-started.png\" alt=\"alt text\" title=\"loom get started\">\n<img src=\"/images/loom/settings.png\" alt=\"alt text\" title=\"loom settings\"></p>\n<h3>Technology:</h3>\n<ul>\n<li>\n<p><code>Less</code> CSS styles.</p>\n</li>\n<li>\n<p><code>Node.js</code> webserver.</p>\n</li>\n<li>\n<p><code>React</code> front end javascript framework.</p>\n</li>\n<li>\n<p><code>Redux</code> state management.</p>\n</li>\n<li>\n<p><code>npm</code> package manager.</p>\n</li>\n<li>\n<p><code>Postgres</code> robust database solution.</p>\n</li>\n</ul>","frontmatter":{"date":"January 01, 2017","path":"/loom","title":"Loom"}}},"pathContext":{}}

